@font-face{font-family:"SIMPLICIFA";font-weight:900;src:local("SIMPLIFICA"),url(/static/media/SIMPLIFICA.41b3c7c2.ttf) format("truetype")}body{font-family:SIMPLICIFA,serif;line-height:1.5;margin:0}body::-webkit-scrollbar{width:.8em}body::-webkit-scrollbar-track{box-shadow:inset 0 0 6px rgba(0,0,0,.3)}body::-webkit-scrollbar-thumb{background-color:#a9a9a9;outline:2px solid #708090}#nav{z-index:100;width:100%;top:0;position:fixed;margin:0;text-align:center;padding-top:.25em;padding-bottom:.25em;transition:background .35s linear;cursor:pointer}.scroll{background:#181d27;transition:background .35s linear}#nav li{display:inline;padding:1em 3em}#nav a{transition:color .1s ease-in-out;display:inline-block;position:relative;overflow:hidden;color:#c4e0e2;text-decoration:none;padding-left:.1em;font-size:3rem;letter-spacing:.1em}#nav a:before{content:"";position:absolute;left:0;right:0;bottom:0;background:#2098d1;height:3px;transition-duration:.1s;transition-timing-function:ease-in-out;-webkit-transform:translateY(3px);transform:translateY(3px);transition-property:-webkit-transform;transition-property:transform;transition-property:transform, -webkit-transform}#nav a:hover:before{-webkit-transform:translateY(0);transform:translateY(0)}#nav a:hover{color:#2098d1;transition:color .1s ease-in-out}#movile-nav-hamburger{display:inline-block;cursor:pointer;float:right;padding:.75em}.bar1,.bar2,.bar3{width:35px;height:5px;background-color:#fff;margin:6px 0;transition:.4s}.change .bar1{-webkit-transform:rotate(-45deg) translate(-9px, 6px);transform:rotate(-45deg) translate(-9px, 6px)}.change .bar2{opacity:0}.change .bar3{-webkit-transform:rotate(45deg) translate(-8px, -8px);transform:rotate(45deg) translate(-8px, -8px)}#mobile-nav{background:#181d27;position:absolute;top:0;right:0;-webkit-align-items:center;align-items:center;height:auto;width:100%;font-family:"Open Sans",sans-serif;cursor:pointer}#mobile-nav-list{position:relative;overflow:hidden;display:block}.mobile-nav-closed{transition:max-height .5s ease;max-height:0}.mobile-nav-opened{transition:max-height .5s ease;max-height:300px}#mobile-nav-list li{padding:.75em}#mobile-nav-list a{color:#c4e0e2;text-decoration:none;display:inline-block;font-size:1.2em}.project-header{text-align:center;font-size:clamp(3rem,4vw,4rem);padding:.25em;color:#c4e0e2;border-style:solid;border-color:gray transparent transparent transparent;border-width:1px;background-color:#181d27}.projects-container{background-color:#fff;max-width:80rem;margin-left:auto;margin-right:auto}.footer{font-family:"Noto Sans KR",sans-serif;position:absolute;left:0;height:20px;padding-top:10px;padding-bottom:10px;width:100%;background-color:#fff;border-top-style:solid;border-top-width:1px;border-top-color:rgba(0,0,0,.125)}.copywrite{font-size:.75em;padding-left:1em;padding-right:1em;opacity:.7}.footer-button{font-family:SIMPLICIFA,serif;position:absolute;display:inline;left:50%;font-size:1.5em;border-style:none;background-color:#fff}ul,li{padding:0;margin:0}
.about-header{background-color:#f5f8fa;opacity:.9;padding-left:2rem;padding-right:2rem;padding-top:2rem;padding-bottom:2rem;border-color:rgba(0,0,0,.125);border-style:solid;border-width:1px}.about-header-icon{display:inherit;height:150px;width:150px;border-radius:50%;border-width:1px;margin-left:auto;margin-right:auto;padding:10px;color:rgba(0,0,0,.125);border-style:solid}.about-main{background-color:#fff;border-color:rgba(0,0,0,.125);border-width:1px}.container{display:inline-block;max-width:44em;padding:2rem;margin-left:auto;margin-right:auto;text-align:left}.content{float:left;margin-left:-1rem;margin-right:-1rem}.col-aboutme{display:block;float:left;width:73%;box-sizing:border-box;padding-left:1em;padding-right:1em}.col-contact{display:block;box-sizing:border-box;width:27%;padding-left:1em;padding-right:1em;float:left}@media only screen and (max-width: 710px){.col-contact,.col-aboutme{width:100%}}.m{margin-top:0;margin-bottom:1em;font-family:"Noto Sans KR",sans-serif}div>.about{font-family:"Noto Sans KR",sans-serif;text-align:center}h1{font-size:4.5em;font-weight:500;margin-top:0;font-family:SIMPLICIFA,serif;line-height:1.3em;margin-bottom:.5rem}h2{font-size:1.7rem;font-weight:300;margin-top:0;margin-bottom:0;opacity:.56;line-height:1.2em}#resume-btn{margin:1em;letter-spacing:.1em;color:#181d27;background-color:#fff;border-color:#181d27;border-radius:5px;margin-bottom:1.825em}#resume-btn:hover{color:#c4e0e2;background-color:#181d27;transition:.2s ease;cursor:pointer}h4{font-family:SIMPLICIFA,serif;font-size:3em;margin:0;padding:5px}span{font-family:"Noto Sans KR",sans-serif;font-size:.65em;padding-left:.25em;padding-right:.75em}.socials-links{font-size:1.75em;display:inline-block;text-decoration:none}a{text-decoration:none;color:#181d27;transition:color .1s ease-in-out}a:hover{color:#2098d1;transition:color .1s ease-in-out}img{max-width:100%}
.hero{position:relative;background-color:#000;height:100vh;width:100%;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.hero::before{content:"";position:absolute;background-color:#000;background-image:url(/static/media/background.199eac09.jpg);background-size:cover;background-position:center bottom;top:0px;right:0px;bottom:0px;left:0px;opacity:.55;overflow:hidden;margin-top:0px}.hero h1{letter-spacing:.15em;position:absolute;color:#c4e0e2;font-size:clamp(2.5rem,10vw,8rem);line-height:.85;text-align:center}.hero p{position:relative;color:#c4e0e2;font-size:clamp(.5rem,5vw,2rem);line-height:.9;text-align:center}
.mobile-project-showcase{position:relative;margin:1em auto;overflow:hidden;width:90%;height:17em;transition:ease;text-align:center;background-color:#181d27}.mobile-desc{color:#c4e0e2}.mobile-icon-on-hover{margin:2.3em}.project-showcase{border-color:rgba(0,0,0,.125);position:relative;margin:1em auto;overflow:hidden;width:90%;height:17em;transition:ease}.project-showcase:hover .icon{right:325px;left:-25px}.project-showcase:hover .icon-on-hover{opacity:1}.project-showcase:hover .desc{width:300px;padding-left:45px;padding-right:45px;background:#181d27;color:#c4e0e2}.project-showcase:hover .desc::after{border-color:transparent #202426 transparent transparent}.icon{position:absolute;height:100%;top:0;left:0;right:300px;z-index:1;background:#181d27;background-position:50% 50%;background-size:cover;transition:right 250ms ease,left 250ms ease}.icon-test{background-image:url(/static/media/unknown.a4b1ea2e.png)}.icon-on-hover{position:relative;padding-top:8em;height:100%;background:rgba(0,0,0,.4);text-align:center;opacity:0;transition:opacity 250ms ease}.desc{position:absolute;width:300px;height:100%;top:0;right:0;z-index:2;background:#f5f8fa;text-align:center;transition:background 250ms ease,color 250ms,width 250ms ease,padding 250ms ease}.desc:after{display:block;width:0;height:0;position:absolute;top:50%;left:-20px;margin-top:-10px;border-style:solid;border-width:10px;border-color:transparent #fff transparent transparent;content:"";transition:border-color 250ms}.desc-type{font-size:1.3em;margin:.56em;font-family:"Noto Sans KR",sans-serif}.desc-name{font-size:2.5em;margin:.3em;opacity:1}.desc-desc{font-family:"Noto Sans KR",sans-serif;margin:.5em;font-size:1.3em}.git-link-button{padding:1em 2.2em;border:.15em solid #fff;color:#fff;text-decoration:none;font-weight:700;transition:background 250ms}.git-link-button:hover{background:rgba(0,0,0,.6);color:#c4e0e2;transition:background 250ms}
